import { Component, OnInit, } from "@angular/core";
import { BasePriceConfigService } from './base-price-config.service';
import { Observable, Subscription } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpErrorResponse, HttpResponse, HttpHeaders } from '@angular/common/http';
import { PluginService } from '../shared/plugin/plugin.service';
import { JhiEventManager, JhiParseLinks } from 'ng-jhipster';
import { CoreErrorHandler } from '../shared/error-handler/error-handler.service';
import { Principal } from '../core';
import { ITEMS_PER_PAGE } from '../shared';
import { ICourses } from '../shared/model/common/courses.model';

declare var $: any;
declare var swal: any;

@Component({
  templateUrl: "./base-price-config.component.html",
  providers: [BasePriceConfigService]
})
export class BasePriceConfigManageComponent implements OnInit {
  courseList: ICourses[];
  courseItem: ICourses = {};
  modeTitleName: string;
  currentAccount: any;
  eventSubscriber: Subscription;
  itemsPerPage: number;
  page: any;
  predicate: any;
  reverse: any;
  totalItems: number;
  totalPages: number;
  links: any;
  disableTitle: boolean;

  constructor(
    public _router: Router,
    public activatedRoute: ActivatedRoute,
    protected eventManager: JhiEventManager,
    protected parseLinks: JhiParseLinks,
    protected principalService: Principal,
    public pluginService: PluginService,
    public errorHandler: CoreErrorHandler,
    public basePriceService: BasePriceConfigService
  ) {
    this.courseList = [];
    this.itemsPerPage = ITEMS_PER_PAGE;
    this.page = 0;
    this.links = {
      last: 0
    };
    this.predicate = 'id';
    this.reverse = false;
  }

  loadCourseList() {
    this.basePriceService
      .getCourses({
        page: this.page,
        size: this.itemsPerPage,
        sort: this.sort()
      })
      .subscribe(
        (res: HttpResponse<ICourses[]>) => this.paginateCourses(res.body, res.headers),
        (res: HttpErrorResponse) => this.onError(res)
      );
  }

  protected paginateCourses(data: ICourses[], headers: HttpHeaders) {
    this.courseList = [];
    this.links = this.parseLinks.parse(headers.get('link'));
    this.totalItems = parseInt(headers.get('X-Total-Count'), 10);
    if (data.length > 0) {
      for (let i = 0; i < data.length; i++) {
        this.courseList.push(data[i]);
      }
    } else {
      this.pluginService.alertMessage("No data found..!", "warning");
    }
  }

  protected onError(errRes: HttpErrorResponse) {
    this.errorHandler.handleError(errRes);
  }

  reset() {
    this.page = 0;
    this.courseList = [];
    this.loadCourseList();
  }

  loadPage(page) {
    this.page = page;
    this.loadCourseList();
  }

  ngOnInit() {
    this.loadCourseList();
    this.principalService.identity().then(account => {
      this.currentAccount = account;
    });
    this.registerChangeInOrganisations();
  }

  ngOnDestroy() {
    this.eventManager.destroy(this.eventSubscriber);
  }

  trackId(index: number, item: any) {
    return item.id;
  }

  registerChangeInOrganisations() {
    this.eventSubscriber = this.eventManager.subscribe('organisationListModification', response => this.reset());
  }

  sort() {
    const result = [this.predicate + ',' + (this.reverse ? 'asc' : 'desc')];
    if (this.predicate !== 'id') {
      result.push('id');
    }
    return result;
  }

  previousPage() {
    this._router.navigateByUrl('/organisation');
  }

  onBack() {
    var localObj = this;
    swal({
      title: "Are you sure to go previous page ?",
      type: "warning",
      showCancelButton: true,
      confirmButtonText: "Yes",
      confirmButtonColor: "#32b432",
      cancelButtonText: "No",
      closeOnConfirm: true,
      closeOnCancel: true
    },
      function (isConfirm) {
        if (isConfirm) {
          localObj.previousPage();
          localObj.pluginService.sideMenuToggle();
        }
      });
  }

  openCourseModal() {
    this.pluginService.showModalWindow("#courseModal");
  }

  closeCourseModal() {
    this.pluginService.hideModalWindow("#courseModal");
  }

  addNewCourse() {
    this.modeTitleName = "Add";
    this.disableTitle = false;
    this.courseItem = {};
    this.openCourseModal();
  }

  editCourse(courseId) {
    this.modeTitleName = "Edit";
    this.disableTitle = true;
    this.courseItem = this.courseList.filter(item => item.id === courseId)[0];
    this.openCourseModal();
  }

  saveCourse() {
    this.subscribeToSaveResponse(this.basePriceService.createCourse(this.courseItem));
  }

  updateCourse() {
    this.subscribeToSaveResponse(this.basePriceService.updateCourse(this.courseItem));
  }

  protected subscribeToSaveResponse(result: Observable<HttpResponse<ICourses>>) {
    result.subscribe((res: HttpResponse<ICourses>) => this.onSaveSuccess(), (res: HttpErrorResponse) => this.onError(res));
  }

  protected onSaveSuccess() {
    if (this.modeTitleName === "Add") {
      this.pluginService.alertMessage("Course created successfully", "success");
    } else if (this.modeTitleName === "Edit") {
      this.pluginService.alertMessage("Course updated successfully", "success");
    }
    this.courseItem = {};
    this.loadCourseList();
    this.closeCourseModal();
  }

  onSubmit() {
    if (this.modeTitleName === "Add") {
      this.saveCourse();
    } else if (this.modeTitleName === "Edit") {
      this.updateCourse();
    }
  }

  resetCourse() {
    this.courseItem = {}
  }

  ngDestroy() {
    this.eventSubscriber.unsubscribe();
  }
}





