import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { RouterModule } from '@angular/router';
import { NgJhipsterModule } from 'ng-jhipster';
import { InfiniteScrollModule } from 'ngx-infinite-scroll';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { SharedModule } from '../shared';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BasePriceConfigManageComponent, BasePriceConfigService } from './index';
import { basePriceRoute } from './base-price-config.route';

@NgModule({
  imports: [
    RouterModule.forChild(basePriceRoute),
    FormsModule,
    ReactiveFormsModule,
    SharedModule,
    NgJhipsterModule,
    InfiniteScrollModule,
    NgbModule
  ],
  declarations: [
    BasePriceConfigManageComponent
  ],
  providers: [BasePriceConfigService],
  schemas: [CUSTOM_ELEMENTS_SCHEMA]
})

export class BasePriceConfigModule {

}