import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { ICourses } from '../shared/model/common/courses.model';
import { environment } from '../../environments/environment';
import { courses } from '../shared/constants/service.constants';
import { Observable } from 'rxjs';
import { createRequestOption } from '../shared';



type EntityResponseType = HttpResponse<ICourses>;
type EntityArrayResponseType = HttpResponse<ICourses[]>;

let headers: HttpHeaders = new HttpHeaders();
headers = headers.append('Access-Control-Allow-Origin', '*').append('Access-Control-Allow-Headers', '*');

@Injectable({ providedIn: 'root' })
export class BasePriceConfigService {
  public courseResourceURL = environment.API_URL + courses;

  constructor(protected http: HttpClient) { }

  createCourse(course: ICourses): Observable<EntityResponseType> {
    return this.http.post<ICourses>(this.courseResourceURL, course, { headers: headers, observe: 'response' });
  }

  updateCourse(course: ICourses): Observable<EntityResponseType> {
    return this.http.put<ICourses>(this.courseResourceURL, course, { observe: 'response' });
  }

  getCourses(req?: any): Observable<EntityArrayResponseType> {
    const options = createRequestOption(req);
    return this.http.get<ICourses[]>(this.courseResourceURL, { params: options, observe: 'response' });
  }
}
